// @require './demo.scss'
// @require '../github-markdown.css'
import React from 'react';
import Tab, { TabPanel } from '../../components/tab/index';

function CheckDoc(props) {
  const { doc, children } = props;

  return doc ? (
    <Tab tabType="border">
      <TabPanel tab="示例">{children}</TabPanel>
      <TabPanel tab="文档">
        <section>
          {doc}
        </section>
      </TabPanel>
    </Tab>
  ) : <div>{children}</div>;
}

export function Demo(props) {
  return (
    <section className="imui-demo">
      <h1>{props.name || '组件名呢？'}</h1>
      <CheckDoc {...props} />
    </section>
  );
}
